<template>
  <div id="note-detail">
    <head-top title="笔记详情"></head-top>
    <note-view :note-item="noteItem" :has-operate="false" is-class>
    </note-view>

  </div>
</template>

<script>
import NoteView from "@/components/r-note/r-note-view";
export default {
  name: "",
  components: { NoteView },
  props: {},
  data() {
    return {
      noteItem: {
        courseID: this.$store.getters.course["CIID"] || "",
        courseName: this.$store.getters.course["CourseName"] || "",
        classHourID: this.$store.getters.course["CHID"] || "",
        classHourName: this.$store.getters.course["CHName"] || "",
        title: this.$route.query.title || "",
        content: this.$route.query.content || "",
        createTime: this.$route.query.createTime || ""
      }
    };
  },
  methods: {}
};
</script>

<style lang='less' scoped>
#note-detail {
  width: 100%;
  height: 100%;
  background: #fff;
}
</style>


